class Navbar extends Component {
  render() {
    return (
      <nav id="navbar">
        <div className="navbar-left">
          <div id="logo">
            <a href="/redcoat">
              <span className="inner">
                <span className="img">
                  <img src={logo}/>
                </span>
                <span>Redcoat</span>
              </span>
            </a>
          </div>
        </div>
        <div className="navbar-centre">Tagging Interface (WIP)</div>
        <div className="navbar-right">
          <div className="dropdown-menu short">
            <a href="features">v1.0</a>
          </div>
        </div>
      </nav>
    )
  }
}

class MainWindow extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {}
    }
  }

  componentWillMount() {

    const fetchConfig = {
        method: 'GET',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        }
      };

    fetch('http://localhost:3000/projects/AlM2c46s/tagging/getDocumentGroup', fetchConfig) // TODO: move localhost out
      .then(response =>
        response.text())
        
      .then((data) => {     
        console.log(data);  
        
        this.setState({data: JSON.parse(data)}, () => { console.log(this.data) })
      });


  }

  render() {

    return (
      <div id="tagging-container">
        <div id="sentence-tagging">
          <div className="sentence">
            <span className="word"><span className="word-inner">Hello</span></span>
            <span className="word"><span className="word-inner">there</span></span>
            <span className="word"><span className="word-inner">haha</span></span>
          </div>
        </div>



      </div>

    )

  }
}

function App() {
  return (
    <div id="app">
      <Navbar/>  
      <div class="tagging"></div>
      <div id="main-container">      
        
        <main id="main">  
          <MainWindow data={data}/> 
        </main>        
      </div>
      <div id="tagging-menu">
          <div className="category-hierarchy">
            <div className="tokens-info">Hello I am wikipedia</div>
            <div id="modify-hierarchy-container">Modify hierarchy container</div>
            <div id="category-hierarchy-tree">Tree</div>


          </div>
        </div>
    </div>
  );
}


export default App;
